import styled, { css } from "styled-components";
import { breakpoints, gutter } from "../../configs/cssConfig";

function getWidthString(i) {
  if (!i) return;

  let width = (i / 12) * 100;
  return `width: ${width}%; flex: 0 0 ${width}%;`;
}

export const Col = styled.div`
  padding: ${gutter}px;
  ${({ xs }) => (xs ? getWidthString(xs) : "width: 100%")};
  @media (min-width: ${breakpoints.extraSmall}px) {
    ${({ sm }) => (sm ? getWidthString(sm) : "width: 100%")};
  }
  @media (min-width: ${breakpoints.medium}px) {
    ${props => console.log(props.md)};
    ${({ md }) => (md ? getWidthString(md) : "width: 100%")};
  }
  @media (min-width: ${breakpoints.large}px) {
    ${({ lg }) => (lg ? getWidthString(lg) : "width: 100%")};
  }
  @media (min-width: ${breakpoints.extraLarge}px) {
    ${({ xlg }) => (xlg ? getWidthString(xlg) : "width: 100%")};
  }
`;
