import styled from "styled-components";
import { breakpoints } from "../../configs/cssConfig";

function getStyleBasedOnView(bp) {
  if (!bp) return;
  if (bp === "md") return `margin-left: auto; padding-right: 2rem;`;
  return `margin: auto;`;
}

export const BurgerContainer = styled.div`
  ${({ xs }) => (xs ? getStyleBasedOnView(xs) : null)};
  @media (min-width: ${breakpoints.small}px) {
    ${({ sm }) => (sm ? getStyleBasedOnView(sm) : null)};
  }
  @media (min-width: ${breakpoints.medium}px) {
    ${({ md }) => (md ? getStyleBasedOnView(md) : null)};
  }
`;
